import React from "react"
import styled from "styled-components"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { theme } from "../theme/GlobalStyles"
import Button from "../components/Button"
import Footer from "../components/footer"

const Wrapper = styled.div`
  display: flex;
  flex-direction: row;
  align-self: center;
  justify-content: center;
  @media (max-width: ${theme.mobile}) {
    flex-direction: column;
    padding-top: 4rem;
  }
`
const Container = styled.div`
  width: 22rem;
  align-self: center;
  @media (max-width: ${theme.mobile}) {
    width: 75%;
  }
`
const Heading = styled.p`
  color: ${theme.darkGray};
  font-family: "Poppins SemiBold";
  font-size: 1.8rem;
`
const Row = styled.div`
  display: flex;
  flex-direction: row;
`
const IndexPage = (props) => {
  const data = useStaticQuery(graphql`
    query {
      image: file(relativePath: { eq: "profile.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
    <Layout>
      <SEO title="Home" />
      <Wrapper>
        <Container>
          <Heading>Hello!</Heading>
          <Heading>It's nice to see you here.</Heading>
          <p>
            Iâ€™m Jade, a junior at Columbia University studying Computer Science and Public Health.
          </p>
          <Row>
            <Button text="More About Me" link="/about"/>
            <Button text="My Projects" link="/projects/lionwash"/>
          </Row>
        </Container>
        <Container>
          <Img fluid={data.image.childImageSharp.fluid} />
        </Container>
      </Wrapper>
      <Footer />
    </Layout>
  )
}
export default IndexPage
